___CSS_LOADER_EXPORT___.push([module.id, `/* Mobile First */
.restaurant-detail {
  grid-template-columns: 1fr;
}

.restaurant-detail__poster {
  max-width: 100%;
}

.restaurant-detail__menus,
.restaurant-detail__reviews {
  grid-column: span 1;
}

@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }

  .app-bar__nav {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
    background-color: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(8px);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    z-index: 1000;
  }

  .app-bar__nav.open {
    display: flex;
  }

  .nav-list {
    flex-direction: column;
    align-items: center;
  }

  .nav-list li {
    margin: 0;
    width: 100%;
  }

  .nav-list a {
    display: block;
    width: 100%;
    text-align: center;
    padding: 16px;
  }

  .footer-nav-list a {
    padding: 16px;
    display: block;
    text-align: center;
  }

  /* Restaurant List */
  .explore-restaurant {
    display: grid;
    grid-template-columns: 1fr;
    gap: 16px;
    padding: 16px;
  }

  /* Restaurant Detail */
  .restaurant-detail {
    grid-template-columns: auto 1fr;
  }
}

@media (min-width: 768px) and (max-width: 1200px) {
  .hero__content h1 {
    font-size: 3rem;
  }

  .hero__content p {
    font-size: 1.5rem;
  }

  .nav-list a,
  .footer-nav-list a {
    padding: 12px 16px;
  }

  /* Restaurant List */
  .explore-restaurant {
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;
    padding: 24px;
  }

  /* Detail Restaurant */
  .restaurant-detail {
    grid-template-columns: 1fr 1fr;
    align-items: start;
  }

  .restaurant-detail__poster {
    grid-column: span 1;
  }

  .restaurant-detail__info,
  .restaurant-detail__overview {
    grid-column: span 1;
  }

  .restaurant-detail__menus,
  .restaurant-detail__reviews {
    padding: 1.5rem;
  }
}

@media (min-width: 1200px) {
  .hero {
    width: 100vw;
    margin: 0;
  }

  .explore {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 16px;
  }

  .nav-list a,
  .footer-nav-list a {
    padding: 12px 16px;
  }

  /* Restaurant List */
  .explore-restaurant {
    grid-template-columns: repeat(3, 1fr);
    gap: 32px;
    padding: 32px;
  }

  /* Detail Restaurant */
  .restaurant-detail {
    grid-template-columns: 1fr 1fr;
    padding: 2rem;
  }

  .restaurant-detail__title {
    grid-column: span 2;
    text-align: left;
    font-size: 2.5rem;
    margin-bottom: 2rem;
  }

  .restaurant-detail__poster {
    width: 100%;
    height: auto;
    border-radius: 12px;
  }

  .restaurant-detail__info,
  .restaurant-detail__overview {
    padding: 2rem;
  }

  .restaurant-detail__menus {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }

  .restaurant-detail__reviews ul {
    padding-left: 0;
  }

  .restaurant-detail__reviews h3 {
    margin-bottom: 1.5rem;
  }
}
